	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"master's student in National Taiwan University, research interests cover Deep Learning, Natural Language Processing, and dialogue systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/EMNLP2018-D3Q/index.md absPath of file >>> MarkdownRemark","html":"<p>This paper presents a Discriminative Deep Dyna-Q (D3Q) approach to improving the effectiveness and robustness of DDQ, a recently proposed framework that extends the Dyna-Q algorithm to integrate planning for task-completion dialogue policy learning. To obviate DDQ’s high dependency on the quality of simulated experiences, we incorporate an RNN-based discriminator in D3Q to differentiate simulated experience from real user experience in order to control the quality of training data. Experiments show that D3Q significantly outperforms DDQ by controlling the quality of simulated experience used for planning. The effectiveness and robustness of D3Q is further demonstrated in a domain extension setting, where the agent’s capability of adapting to a changing environment is tested.</p>","fields":{"tagSlugs":["/tags/dialogue/","/tags/dialogue-policy/","/tags/reinforcement-learning/"]},"frontmatter":{"title":"Discriminative Deep Dyna-Q: Robust Planning for Dialogue Policy Learning","tags":["Dialogue","Dialogue Policy","Reinforcement Learning"],"date":"2018-08-13T22:40:32.169Z","description":"Shang-Yu Su, Xiujun Li, Jianfeng Gao, Jingjing Liu and Yun-Nung Chen","conference":"EMNLP 2018"}}},"pathContext":{"slug":"/posts/emnlp2018-d3q/"}}

